import type { ComponentPropsWithoutRef, ElementType } from 'react';
import React from 'react';
import styled from 'styled-components';

export interface ContainerProps
  extends ComponentPropsWithoutRef<React.ElementType> {
  /**
   * Container의 HTML 태그
   *
   * @default 'div'
   */
  as?: ElementType;
  /**
   * 최대 넓이 (number로 입력시 px로 고정, string으로 입력시 그대로 반영)
   *
   * @default '100%'
   */
  maxWidth?: number | string;
  /**
   * 최소 넓이 (number로 입력시 px로 고정, string으로 입력시 그대로 반영)
   *
   * @default '100px'
   */
  minWidth?: number | string;
}

const Container = ({
  maxWidth = '100%',
  minWidth = '100px',
  as = 'div',
  children,
  ...attributes
}: ContainerProps) => {
  return (
    <FlexStyle as={as} {...attributes} maxWidth={maxWidth} minWidth={minWidth}>
      {children}
    </FlexStyle>
  );
};

const FlexStyle = styled.div<ContainerProps>`
  display: block;

  max-width: ${({ maxWidth }) =>
    typeof maxWidth === 'number' ? `${maxWidth}px` : maxWidth};
  min-width: ${({ minWidth }) =>
    typeof minWidth === 'number' ? `${minWidth}px` : minWidth};

  margin-left: auto;
  margin-right: auto;

  white-space: normal;
`;

export default Container;
